import { Controller } from '@ali/4ever-cangjie';
import { FONTS_SET } from '../actions';
import setFonts from '../commands/setFonts';

export default function onAction(action, controller: Controller, next) {
  const { type, payload } = action;
  if (type === FONTS_SET) {
    const { fonts } = payload;
    setFonts(controller, fonts);
  }
  return next();
}
